h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
}

body {
  color: var(--main-text-color);
  background-color: var(--page-color);
  letter-spacing: .02em;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  line-height: 1.14;
}

.list {
  padding-left: 0;
  list-style-type: none;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.conteiner {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding: 0 15px;
}

@media screen and (min-width: 320px) {
  .conteiner {
    max-width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .conteiner {
    max-width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .conteiner {
    max-width: 1280px;
  }
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .2);
  justify-content: center;
  align-items: center;
  transition: opacity .25s, visibility .25s;
  display: flex;
  position: fixed;
  top: 0;
}

